body {
  /* background: linear-gradient(90deg, #dfe6e9 50%, #ffffff 50%); */
  font-family: 'Playfair Display', serif;
}

p {
  font-family: sans-serif;
}

.app-header {
  margin: 1rem 5vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.logo {
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.nav {
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.navLink {
  margin-left: 1rem;
  text-decoration: line-through;
}

.app {
  text-align: center;
}

.intro {
  margin: 12vh auto 0;
  width: 95vw;
  max-width: 900px;
}

.intro h1 {
  font-size: 52px;
  line-height: 67px;
}

.emphasis {
  background-color: #FF7F80;
  color: #ffffff;
}

.emphasis:hover {
  color: #FF7F80;
  background-color: rgba(0,0,0,0);
  /* cursor: pointer; */
  -webkit-transition: 0.25s ease-out;
  -o-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
}


